import React, { useState, useEffect, useCallback } from 'react'
import styled from 'styled-components'
import { Heading, Card, CardBody, Button, useModal, TuneIcon } from '@pancakeswap/uikit'
import { useWeb3React } from '@web3-react/core'
import { getCakeAddress } from 'utils/addressHelpers'
import { getBalanceNumber } from 'utils/formatBalance'
import { useTranslation } from 'contexts/Localization'
import useGetLotteryHasDrawn from 'hooks/useGetLotteryHasDrawn'
import useTokenBalance from 'hooks/useTokenBalance'
import { useMultiClaimLottery } from 'hooks/useBuyLottery'
import { useTotalClaim } from 'hooks/useTickets'
import { useLotteryAllowance } from 'hooks/useAllowance'
import { useApproval } from 'hooks/useApproval'
import PurchaseWarningModal from 'views/Lottery/components/TicketCard/PurchaseWarningModal'
import BuyTicketModal from 'views/Lottery/components/TicketCard/BuyTicketModal'
import UnlockButton from 'components/UnlockButton'
import CakeWinnings from './CakeWinnings'
import LotteryJackpot from './LotteryJackpot'
import './WalletCard.scss'

const StyledWalletCard = styled(Card)`
  // background-image: url('/images/ticket-bg.svg');
  background-repeat: no-repeat;
  background-position: top right;
  // background-color: #dc4734;
  min-height: 376px;
`

const Block = styled.div`
  margin-bottom: 16px;
`

const CardImage = styled.img`
  margin-bottom: 16px;
`

const Label = styled.div`
  // color: ${({ theme }) => theme.colors.textSubtle};
  color: #fff
  font-size: 14px;
`

const Actions = styled.div`
  display: flex;
  margin-top: 24px;
  button {
    flex: 1 0 50%;
  }
`

const CardLogo = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;

  margin: 24px 0;
`

const ImageLogo = styled.img`
  display: flex;
  width: 30%;

  // margin-top: 24px;
`

const WalletCard = () => {
  const { account } = useWeb3React()
  const lotteryHasDrawn = useGetLotteryHasDrawn()
  const [requestClaim, setRequestedClaim] = useState(false)
  const [timer, setTimer] = useState({ days: '00', hours: '00', minutes: '00', seconds: '00' })
  const [isTimerExpired, setTimerExpired] = useState(false)
  const { t } = useTranslation()
  const allowance = useLotteryAllowance()
  const [onPresentApprove] = useModal(<PurchaseWarningModal />)
  const { claimAmount, setLastUpdated } = useTotalClaim()
  const { onMultiClaim } = useMultiClaimLottery()
  const { balance: cakeBalance } = useTokenBalance(getCakeAddress())
  const { handleApprove, requestedApproval } = useApproval(onPresentApprove)

  const handleClaim = useCallback(async () => {
    try {
      setRequestedClaim(true)
      const txHash = await onMultiClaim()
      // user rejected tx or didn't go thru
      if (txHash) {
        setRequestedClaim(false)
        setLastUpdated()
      }
    } catch (e) {
      console.error(e)
    }
  }, [onMultiClaim, setRequestedClaim, setLastUpdated])

  const renderLotteryTicketButtonBuyOrApprove = () => {
    if (!allowance.toNumber()) {
      return (
        <Button width="100%" disabled={requestedApproval} onClick={handleApprove}>
          {t('Approve CAKE')}
        </Button>
      )
    }
    if (lotteryHasDrawn) {
      return <Button disabled> {t('On sale soon')}</Button>
    }
    return (
      <Button id="dashboard-buy-tickets" variant="secondary" onClick={onPresentBuy}>
        {t('Buy Tickets')}
      </Button>
    )
  }

  const [onPresentBuy] = useModal(<BuyTicketModal max={cakeBalance} />)

  const getTimeRemaining = (endtime) => {
    const tempTime = Date.parse(endtime) - Date.parse(new Date().toString())
    const seconds = Math.floor((tempTime / 1000) % 60)
    const minutes = Math.floor((tempTime / 1000 / 60) % 60)
    const hours = Math.floor((tempTime / (1000 * 60 * 60)) % 24)
    const days = Math.floor(tempTime / (1000 * 60 * 60 * 24))
    return {
      total: tempTime,
      days,
      hours,
      minutes,
      seconds,
    }
  }

  const initializeClock = useCallback((endtime) => {
    const updateClock = () => {
      const tempTime = getTimeRemaining(endtime)

      if (tempTime.total <= 0) {
        clearInterval(timeinterval)
        setTimerExpired(true)
        setTimer({ days: '00', hours: '00', minutes: '00', seconds: '00' })
        return true
      }

      // const { days } = tempTime
      const days = `0${tempTime.days}`.slice(-2)
      const hours = `0${tempTime.hours}`.slice(-2)
      const minutes = `0${tempTime.minutes}`.slice(-2)
      const seconds = `0${tempTime.seconds}`.slice(-2)

      const timerObject = { days, hours, minutes, seconds }
      // console.log('timerObject', timerObject)

      setTimer(() => timerObject)

      return false
    }

    updateClock()
    const timeinterval = setInterval(updateClock, 1000)
  }, [])

  useEffect(() => {
    // const deathline = new Date(Date.parse(new Date().toString()) + 20 * 1000) // for endless timer
    const deathline = 'June 12 2021 14:00:00 GMT+0300'

    initializeClock(deathline)
  }, [initializeClock])

  return (
    <StyledWalletCard>
      <CardBody>
        <div>
          <div className="presale">
            <div className="presale-title">
              {/* TODO change background-color */}
              <div className="logo">
                <img
                  src="https://i.ibb.co/VNSb1R2/logo-cut.png"
                  // src="https://i.ibb.co/Sr8dqrt/logo-dark-cut.png"
                  height="80"
                  alt="Icon"
                />
              </div>
              <div>LIONSWAP PRE-SALE</div>
            </div>
            <div className="presale-timer">
              <div id="countdown" className="countdown">
                <div className="countdown__item">
                  <div className="countdown__number">
                    <span className="days">{timer.days}</span>
                    <p className="days_text">Days</p>
                  </div>
                  <div className="seperator countdown__separator">:</div>
                </div>
                <div className="countdown__item">
                  <div className="countdown__number">
                    <span className="days">{timer.hours}</span>
                    <p className="days_text">Hours</p>
                  </div>
                  <div className="seperator countdown__separator">:</div>
                </div>
                <div className="countdown__item">
                  <div className="countdown__number">
                    <span className="days">{timer.minutes}</span>
                    <p className="days_text">Minutes</p>
                  </div>
                  <div className="seperator countdown__separator">:</div>
                </div>
                <div className="countdown__item">
                  <div className="countdown__number">
                    <span className="days">{timer.seconds}</span>
                    <p className="days_text">Seconds</p>
                  </div>
                </div>
              </div>
            </div>
            <div className="presale-stages">
              <div id="stages">
                <div className="stage filled" style={{ width: '36%' }}>
                  <p className="money">
                    <span>$</span> 100,000
                  </p>
                  <p className="title">Pre Sale</p>
                </div>
                <div className="separator" />
                {/* <div className="stage filled2" style={{width: '28%'}}>
                <p className="money">
                  <span>$</span> 200,000
                </p>
                <p className="title">Pre-Sale</p>
              </div>
              <div className="separator"></div> */}
                <div className="stage" style={{ width: '24%' }}>
                  <p className="money">
                    <span>$</span> 170,000
                  </p>
                  <p className="title">Soft Cap</p>
                </div>
                <div className="separator" />
                <div className="stage last" style={{ width: '12%' }} />
              </div>
            </div>
            <Block>
              <div className="presale-price">1 LION - 0.001 BNB (min. buy 0.1 BNB max. buy 2 BNB)</div>
            </Block>
            {/* <a
              href="https://exchange.pancakeswap.finance/#/swap?outputCurrency=0x4bf0b64664c4625cd3f15968a8400165962cb728"
              target="_blank"
              rel="noreferrer"
            >
              <button type="submit" className="btn buy-btn">
                Buy
              </button>{' '}
            </a> */}
            <Actions>
              <Button disabled={!isTimerExpired} width="100%">
                {t('Buy')}
              </Button>
            </Actions>
          </div>
        </div>
      </CardBody>
    </StyledWalletCard>
  )
}

export default WalletCard
